import React from "react";

import { StyledHome } from '../styles';
import Animated from './Animated';
import Stack from './AnimatedStack';


function Home() {
  return (
    <>
    <StyledHome>
        <Stack />
      <div className="home-contents">
        <div className="welcome-container">
          <Animated />
        </div>
      </div>
      
    </StyledHome>
    </>
  );
}

export default Home;